nav {
  background: rgba(0, 0, 0, 0.5);
  width: max-content;
  display: flex;
  flex-direction: column;
  padding: 1.7rem .7rem;
  position: fixed;
  top: 30%;
  right: 1rem;
  z-index: 2;
  display: flex;
  gap: 0.8rem;
  border-radius: 3rem;
  backdrop-filter: blur(8px);
}

nav a {
  background: transparent;
  padding: 0.7rem;
  border-radius: 50%;
  display: flex;
  font-size: 1.1rem;
  color: var(--light);
}


nav a:hover {
  background-color: rgba(0, 0, 0, 0.3);
}

nav a.active {
  background: var(--light);
  color: var(--white);
}
