.servicesBoxes {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 4rem;
  margin-top: 2rem;
}

.servicesBox {
  background-color: var(--bg-vice);
  border-radius: 0 0 20px 20px;
  border: 1px solid var(--light);
  transition: var(--transition);
  height: fit-content;
}
.servicesBox:hover {
  background-color: transparent;
  border-color: var(--bg-vice);
}

.servicesBox .top {
  background-color: var(--light);
  height: 4rem;
  border-radius: inherit;
  display: grid;
  place-items: center;
}

.servicesBox:hover .top {
  box-shadow: 3px 3px 8px var(--light);
  border: 1px solid var(--light);
}

.top h3 {
  font-size: 0.9rem;
  color: var(--bg);
}

.bottom {
  height: 80%;
  padding: 3rem 1.2rem;
  color: var(--light);
}

.bottom li {
  display: flex;
  align-items: start;
  justify-content: space-between;
  margin: 0.8rem 0;
  color: var(--primary-color);
}

.serviceIcon {
  font-size: 1.5rem;
  margin-right: 8px;
}

@media screen and (max-width: 920px) {
  .servicesBoxes {
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
  .servicesBox{
      height: auto;
  }
}

@media screen and (max-width: 600px) {
  .servicesBoxes {
    grid-template-columns: 1fr;
    gap: 2rem;
  }
  
.bottom li {
  justify-content: center;
}

}
