.aboutBox {
  display: grid;
  grid-template-columns: 35% 50%;
  grid-gap: 15%;
  padding-top: 4rem;
  /* height: 80vh; */
}

.imageBox {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: var(--light);
  display: grid;
  place-items: center;
}

.myImage {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition: var(--transition);
  height: 100%;
  width: 100%;
  box-shadow: 2px 2px 8px rgba(0, 0, 0, 0.8);
}

.myImage:hover {
  transform: rotate(0);
}

/* Content */
.boxes {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
  margin-bottom: 20px;
}

.box {
  height: 150px;
  width: 170px;
  display: grid;
  place-items: center;
  background: var(--bg-vice);
  padding: 2rem;
  text-align: center;
  border: 2px solid transparent;
  border-radius: 10px;
  transition: var(--transition);
}

.box:hover {
  background: transparent;
  border-color: var(--second-color);
}

.box .icon {
  font-size: 1.4rem;
  margin-bottom: 1rem;
  color: var(--primary-color);
}

.box h5 {
  font-size: 0.95rem;
}

.box small {
  font-size: 0.7rem;
  color: var(--light);
}

.aboutBox p {
  margin: 2rem 0 2.5rem;
  color: var(--light);
}

/* Media Querries for Header.jsx */
/* For Tablets */
@media screen and (max-width: 1024px) {
  .aboutBox {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .imageBox {
    width: 50%;
    margin: 2rem auto 4rem;
  }
  p {
    margin: 1rem 0 1.5rem;
    text-align: center;
  }
  .content {
    text-align: center;
  }
}

/* For Phone */
@media screen and (max-width: 600px) {
  .exprienceBox{
    gap: 1rem;
  }
  .imageBox {
    width: 65%;
    margin: 0 auto 3rem;
  }
  #about {
    margin-top: 0;
  }
  .boxes {
    grid-template-columns: 1fr 1fr;
  }

  .content {
    text-align: center;
  }
  .content p {
    margin: 1.2rem 0;
  }
}
