#footer {
  height: 55vh;
  background-color: var(--primary-color);
  color: var(--bg);
  position: relative;
}

.footerBox {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
}

.footerLogo {
  text-align: center;
  color: var(--bg);
  font-size: 3rem;
  font-weight: 500;
  margin-bottom: 2rem;
}

.footerLinks {
  margin: 1.2rem 0;
}

.footerLinks a {
  color: var(--light);
  margin: 0 1.2rem;
}

.footerLinks a:hover{
    color: var(--bg);
}

.footerNav {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1.2rem 0;
}

.footerNav a {
  background-color: var(--bg);
  color: var(--light);
  padding: 0.6rem;
  border-radius: .5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 1.2rem;
  border: 1px solid transparent;
  transition: 0.3s ease-out;
}

.footerNav a:hover {
    border-color: var(--bg);
    background-color: transparent;
    color: var(--bg);
}

#footer p{
    position: absolute;
    bottom: 2px;
}

