.experienceBox {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.2rem;
  justify-content: center;
  align-items: center;
  padding-top: 2rem;
}

.frontend,
.backend {
  height: 400px;
  width: 100%;
  border-radius: 20px;
  padding: 2rem;
  transition: var(--transition);
  background: var(--bg-vice);
}

.frontend h3, .backend h3 {
  text-align: center;
  color: var(--primary-color);
}

.frontend:hover,
.backend:hover {
  background: transparent;
  border: 1px solid var(--light);
}

.frontendTech,
.backendTech {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  place-items: center;
  height: 100%;
}

.tech {
  display: flex;
  gap: 10px;
  align-items: start;
}

.techIcon {
  color: var(--primary-color);
}

.tech small {
  color: var(--light);
}

@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-md);
  }
  .experienceBox {
    grid-template-columns: 1fr;
    width: 80%;
  }
}

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-sm);
  }
  .experienceBox {
    grid-template-columns: 1fr;
  }
  .frontend,
  .backend {
    height: 300px;
  }
}
