.portfolioBox {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.5rem;
  padding-top: 2.5rem;
}

.portfolioCard {
  background-color: var(--bg-vice);
  padding: 1.3rem;
  border-radius: 1rem;
  border: 1px solid transparent;
  transition: var(--transition);
}

.portfolioCard:hover {
  border-color: var(--second-color);
  background: transparent;
}

.cardImage {
  border-radius: 1rem;
  overflow: hidden;
  height: 120px;
}

.portfolioCard h3 {
  margin: 1rem 0;
  font-size: 1rem;
}

.cardBtn {
  display: flex;
  gap: 1rem;
  margin-bottom: 1rem;
}

@media screen and (max-width: 1024px) {
  .portfolioBox {
    grid-template-columns: 1fr 1fr;
    gap: 1.2rem;
  }
}

@media screen and (max-width: 600px) {
  .portfolioBox {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
}
