* {
  padding: 0;
  margin: 0;
  border: 0;
  outline: 0;
  list-style-type: none;
  text-decoration: none;
  box-sizing: border-box;
}

:root {
  --bg: #1f1f38;
  --bg-vice: #2c2c6c;
  --primary-color: #4db5ff;
  --second-color: rgba(77, 181, 255, 0.4);
  --white: #fff;
  --light: rgba(255, 255, 255, 0.6);
  --transition: all 400ms ease;

  /* ********** Container-Width */
  --container-lg: 75%;
  --container-md: 86%;
  --container-sm: 90%;
}

html {
  scroll-behavior: smooth;
  scroll-margin-top: 4rem;
}

::-webkit-scrollbar {
display: none;
}


body {
  background-color: var(--bg);
  color: var(--white);
  line-height: 1.4;
  font-family: Poppins;
}

.container {
  width: var(--container-lg);
  margin: auto;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  padding-top: 4rem;
  height: auto;
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--light);
}

.text-light {
  color: var(--primary-color);
}

a {
  color: var(--primary-color);
  transition: var(--transition);
}

a:hover {
  color: var(--white);
}

.btn {
  width: max-content;
  display: inline-block;
  color: var(--primary-color);
  padding: 0.75rem 1.2rem;
  border: 0.04rem;
  cursor: pointer;
  border: 1px solid var(--primary-color);
  transition: var(--transition);
}

.btn:hover {
  background-color: var(--white);
  color: var(--bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--primary-color);
  color: var(--bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

@media screen and (max-width: 1024px){
    .container{
        width: var(--container-md);
    }
}

@media screen and (max-width: 600px){
    .container{
        width: var(--container-sm);
    }
}
