.contactBox {
  display: grid;
  grid-template-columns: 30% 65%;
  padding-top: 3rem;
  gap: 5%;
}

.contactLeft {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.contactCard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: auto;
  width: 70%;
  text-align: center;
  margin: 0.5rem auto;
  background-color: var(--bg-vice);
  padding: 1rem 1.2rem;
  border-radius: 1rem;
  border: 1px solid transparent;
  transition: 0.3s ease-out;
}

.contactCard:hover {
  background-color: transparent;
  border-color: var(--light);
}

.contactIcon {
  font-size: 1.4rem;
}

.contactCard small {
  color: var(--light);
  font-size: 0.8rem;
  margin: 0.8rem 0;
}

.contactCard a:last-child {
  font-size: 0.8rem;
}

.contactBox form {
  width: 100%;
}

form input,
form textarea {
  width: 100%;
  height: 3rem;
  margin-bottom: 2rem;
  background-color: transparent;
  padding: 0.8rem 1.2rem;
  border: 1px solid var(--light);
  border-radius: 1rem;
  font-size: 1rem;
  color: #fff;
}

form textarea {
  height: 200px;
}

form input::placeholder,
textarea::placeholder {
  color: var(--light);
}

form input:focus {
  background: var(--bg-vice);
  color: var(--white);
}
form textarea:focus {
  background: var(--bg-vice);
  color: var(--white);
}

form button {
  outline: none;
  border: none;
  padding: 1rem 4rem;
  width: 100%;
  cursor: pointer;
  font-size: 1rem;
  border-radius: 1rem;
  transition: 0.3s ease-in-out;
}

form button:hover {
  background-color: var(--bg-vice);
  color: var(--light);
}

@media screen and (max-width: 1024px) {
  .contactBox {
    display: grid;
    grid-template-columns: 1fr;
    padding-top: 1rem;
    gap: 1rem;
  }

  .contactCard {
    width: 100%;
  }

  .contactCard small {
    font-size: 1.2rem;
  }

  .contactCard h4 {
    font-size: 1.2rem;
  }

  .contactIcon {
    font-size: 2rem;
    margin: 1.2rem 0;
  }

  .contactCard a:last-child {
    font-size: 1rem;
  }
}
