header {
  height: 100vh;
  padding-top: 4rem;
  overflow: hidden;
}

header .headerBox {
  text-align: center;
  height: 100%;
  position: relative;
}

h1{
    margin-bottom: 3px;
}

/* headerButtons */
.headerButtons {
  margin-top: 2rem;
  display: flex;
  gap: 1.4rem;
  justify-content: center;
  align-items: center;
}

/* Header Socials */
.socials {
  position: absolute;
  display: flex;
  align-items: center;
  flex-direction: column;
  left: 0;
  bottom: 2rem;
}

.socials a {
  font-size: 1.2rem;
  margin: 10px 0;
}

.socials a:hover {
  transform: scale(1.1);
}

.socials::after {
  content: "";
  height: 2rem;
  width: 2px;
  background-color: var(--primary-color);
}


/* Image of myself */
.me {
  width: 20rem;
  height: 22rem;
  position: absolute;
  left: calc(50% - 10rem);
  border-radius: 15px;
  overflow: hidden;
  margin-top: 4rem;
}

.me img{
    transition: var(--transition);
    filter: grayscale(0.2);
}

.me:hover img {
  transform: scale(1.1);
  filter: grayscale(0);
}

/* scrollDown Button */
.scrollDown{
    position: absolute;
    right: 0;
    bottom: 4rem;
    transform: rotate(90deg);
}


/* Media Querries for Header.jsx */
/* For Tablets */
@media screen and (max-width:1024px){
    header{
        height: 68vh;
    }
}

/* For Phone */
@media screen and (max-width:600px){
    header{
        height: 60vh;
        padding-top: 7rem;
    }

    .socials, .scrollDown{
        display: none;
    }
}