.testimonyCards {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 450px;
  height: auto;
  background-color: var(--bg-vice);
  padding: 1.2rem 1rem;
  margin-top: 2rem;
  border-radius: 1rem;
  text-align: center;
  position: relative;
  border: 1px solid transparent;
  transition: 0.3s ease-out;
  cursor: pointer;
}

.testimonyCards:hover {
  border-color: var(--light);
  background-color: transparent;
}

.testimonyImage {
  height: 85px;
  aspect-ratio: 1/1;
  margin-bottom: 0.8rem;
}

.testimonyImage img {
  border-radius: 50%;
  border: 5px solid rgba(44, 15, 82, 0.438);
  transition: 0.3s ease-out;
}

.testimonyCards:hover img {
  border: 5px solid rgba(221, 221, 221, 0.438);
}

.testimonyCards p {
  font-size: 0.9rem;
  color: var(--light);
  margin-bottom: 2rem;
}

.testimonyCards h3 {
  position: absolute;
  right: 3rem;
  bottom: 0.7rem;
  color: var(--light);
}

@media screen and (max-width: 1024px) {
  .testimonyCards p {
    font-size: 1.2rem;
  }

  .testimonyImage {
    height: 140px;
    aspect-ratio: 1/1;
  }
}
@media screen and (max-width: 600px) {
  .testimonyCards p {
    font-size: 1.2rem;
  }

  .testimonyImage {
    height: 140px;
    aspect-ratio: 1/1;
  }
}
